import Toast from '../components/toast/toast';

export default {
  toast: Toast,
  /**
   * 
   * format(ms,'yyyy-MM-dd hh:mm:ss')
   * @param time 
   * @param fmt 
   */
  format(time, fmt) {
    let d = new Date(time + 8 * 3600 * 1000);
    let o = {
      "M+": d.getUTCMonth() + 1,                 //
      "d+": d.getUTCDate(),                    //
      "h+": d.getUTCHours(),                   //
      "m+": d.getUTCMinutes(),                 //
      "s+": d.getUTCSeconds(),                 //
      "q+": Math.floor((d.getUTCMonth() + 3) / 3), //
      "S": d.getUTCMilliseconds()             //
    };
    if (/(y+)/.test(fmt))
      fmt = fmt.replace(RegExp.$1, (d.getUTCFullYear() + "").substr(4 - RegExp.$1.length));
    for (let k in o)
      if (new RegExp("(" + k + ")").test(fmt))
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  }, 
  /**
   * object
   *
   * @param {*} object
   * @returns
   */
  isEmptyObject(object) {
    let t;
    for (t in object)
      return true;
    return false;
  },
  /**
   * url
   * @param param 
   * @returns {*}
   */
  getUrlParams(param) {
    let ps = decodeURI(window.location.href);
    if (ps === '') return '';
    let params = (ps.substr(ps.lastIndexOf("?") + 1)).split("&");
    if (params !== null) {
      for (let i = 0; i < params.length; i++) {
        let strs = params[i].split("=");
        if (strs[0] === param && strs[1]) {
          return strs[1];
        }
      }
    }
    return "";
  },
  /**
   *  1 => 
   * 100
   */
  numberToZh(num) {
    let arr = ['', '', '', '', '', '', '', '', '', ''];
    if (num < 11) return arr[num - 1];
    let str = '';
    (num + '').split('').forEach((val, idx) => {
      val = Number(val);
      if (idx === 0) {
        if (val === 1) {
          str += arr[9];
        } else {
          str += arr[val - 1];
          str += arr[9];
        }
      } else {
        str += arr[val - 1];
      }
    });
    return str;
  },
  // 
  focusToLast(input) {
    let len = input.value.length;
    if (document.selection) {
      let sel = input.createTextRange();
      sel.moveStart('character', len);
      sel.collapse();
      sel.select();
    } else if (typeof input.selectionStart === 'number' && typeof input.selectionEnd === 'number') {
      input.selectionStart = input.selectionEnd = len;
    }
  }
}


